import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../services/client.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages'
import { Client } from '../../models/Client';

@Component({
  selector: 'app-client-details',
  templateUrl: './client-details.component.html',
  styleUrls: ['./client-details.component.css']
})
export class ClientDetailsComponent implements OnInit {
  id: string;
  client: Client;
  hasBalance: boolean = false;
  showBalanceUpdateInput: boolean = false;
  myStyle: object = {};
  myParams: object = {};
  width: number = 100;
  height: number = 100;
  random: number;
  urlstr: string;

  constructor(
    private clientService: ClientService,
    private router: Router,
    private route: ActivatedRoute,
    private flashMessage: FlashMessagesService
  ) { }

  ngOnInit() {
    this.random = Math.floor(Math.random() * Math.floor(23));
    this.urlstr = 'url("../assets/p'+this.random+'.jpg")';
    
    this.myStyle = {
      'position': 'fixed',
      'width': '100%',
      'height': '100%',
      'z-index': -1,
      'top': 0,
      'left': 0,
      'right': 0,
      'bottom': 0,
      'background-image': this.urlstr,
      'background-color': '#d5e1d9',
      'background-repeat': 'no-repeat',
      'background-size': '100% 100%'
    };

    this.myParams = {
      particles: {
        number: { value: 200, density: { enable: true, value_area: 800 } },
        color: { value: "#645d5d" },
        shape: {
          type: "edge",
          stroke: { width: 0, color: "#000000" },
          polygon: { nb_sides: 3 },
          image: { src: "img/github.svg", width: 100, height: 100 }
        },
        opacity: {
          value: 0.5,
          random: false,
          anim: { enable: false, speed: 1, opacity_min: 0.1, sync: false }
        },
        size: {
          value: 0,
          random: true,
          anim: {
            enable: false,
            speed: 0,
            size_min: 1.6230472712517763,
            sync: false
          }
        },
        line_linked: {
          enable: true,
          distance: 64.09588744762158,
          color: "#ffffff",
          opacity: 0.5528270292357362,
          width: 1
        },
        move: {
          enable: true,
          speed: 6,
          direction: "none",
          random: true,
          straight: false,
          out_mode: "out",
          bounce: false,
          attract: { enable: false, rotateX: 600, rotateY: 1200 }
        }
      },
      interactivity: {
        detect_on: "canvas",
        events: {
          onhover: { enable: true, mode: "repulse" },
          onclick: { enable: true, mode: "repulse" },
          resize: true
        },
        modes: {
          grab: { distance: 400, line_linked: { opacity: 1 } },
          bubble: { distance: 400, size: 40, duration: 2, opacity: 8, speed: 3 },
          repulse: { distance: 200, duration: 0.4 },
          push: { particles_nb: 4 },
          remove: { particles_nb: 2 }
        }
      },
      retina_detect: false
    }
    //Get id from url
    this.id = this.route.snapshot.params['id'];
    //Get the client
    this.clientService.getClient(this.id).subscribe(client => {
      if(client != null) {
        if(client.balance > 0) {
          this.hasBalance = true;
        }
      }
      this.client = client;
    });
  }

  updateBalance() {
    this.clientService.updateClient(this.client);
    this.flashMessage.show('Balance updated', {
      cssClass: 'alaert-success' , timeout: 4000
    });
  }

  onDeleteClick(){
    if(confirm('Are you sure?')) {
      this.clientService.deleteClient(this.client);
      this.flashMessage.show('Client removed', {
        cssClass:'alert-success',timeout: 4000
      });
      this.router.navigate(['/']);
    }
  }

}
